*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container{
  width:100%;
  max-width:1400px;
  margin: 0 auto;
}

nav{
  width:100%;
  background: #333;
  padding: 20px 0;
}

nav .container form button{
  background: #5486e0;
  color: #fff;
  cursor:pointer;
}

nav .container form button,
nav .container form input{
  border:none;
  outline:none;
  padding:0 10px;
  height:100px;
}

.hero{
  width:100%;
  padding: 30px 0;
  min-height: calc(100vh - 74px);
  position: relative;
}

.hero .container{
  color:red;
  font-size: 30px;
  line-height: 1;
}

.hero .container img{
  width: 100%;
  height: auto;
  object-fit: cover;
}

.loading{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 10px;
  background: #3498db;
  border-radius: 5px;
  animation: spinner 1.8s ease-in-out infinite;
}

.loading::before{
  left: 10px;
  top: -20px;
  width: 40px;
  background: #ef4836;
}

.loading::after{
  bottom: -20px;
  width: 35px;
  background: #f5ab35;
}

.loading::before, .loading::after{
  position: absolute;
  display: block;
  content: "";
  animation: spinner 1.8s ease-in-out infinite;
  height: 10px;
  border-radius: 5px;
}

@keyframes spinner{
  0%{
    transform: translateX(40px);
  }

  50%{
    transform:translateX(-30px);
  }

  100%{
    transform: translateX(40px);
  }
}

@media (max-width: 1462px){
  .container{
    max-width: 92%;
  }
}